.grid-area {
  display: grid;
  grid-template-rows: repeat(20, 28px); /* increased from 20px */
  gap: 1px;
  margin: 20px auto;
}

.row {
  display: grid;
  grid-template-columns: repeat(20, 28px); /* match row height */
}

.tile {
  width: 28px;
  height: 28px;
  background-color: #333;
  border: 1px solid #222;
  position: relative;
}

.tile.yama {
  background-color: #880000;
}
.tile.player {
  background-color: #00ff00 !important; /* bright green */
  z-index: 10;
}
.tile.bomb {
  background-color: orange;
  animation: pulse 0.6s infinite alternate;
}
.tile.explosion {
  background-color: red !important;
  animation: flash 0.3s ease-in-out;
}
.tick-label {
  position: absolute;
  font-size: 10px;
  color: white;
  top: 1px;
  left: 3px;
  pointer-events: none;
}
.controls,
.movement-buttons,
.counters {
  display: flex;
  gap: 10px;
  align-items: center;
  flex-wrap: wrap;
}
.log-output {
  max-height: 150px;
  overflow-y: scroll;
  background-color: #111;
  color: #fff;
  padding: 8px;
  font-family: monospace;
  border: 1px solid #333;
}
@keyframes pulse {
  0% { opacity: 1; }
  100% { opacity: 0.6; }
}
@keyframes flash {
  0% { background-color: yellow; }
  50% { background-color: red; }
  100% { background-color: red; }
}


.tile.wave {
  background-color: #0077cc;
  animation: wave-pulse 0.4s ease-in-out;
  z-index: 1;
}

@keyframes wave-pulse {
  0% {
    background-color: #0077cc;
  }
  50% {
    background-color: #33aaff;
  }
  100% {
    background-color: #0077cc;
  }
}
